  <div id="app">
    <router-view></router-view>


    <!--  -->

    <!-- <div style="text-align:center">
      <router-link to="/"></router-link>
      <router-link to="/second"></router-link>
    </div>
    <router-view class="view" v-bind:now="nowdate" v-on:dataFromRouterTemp="listenToRouterTemp"></router-view> -->
  </div>

</template>

<script>
import footerTemp from './component/footerTemp.vue'
import routerTemp from './component/routerTemp.vue'
import drcell    from './component/cell/cell.vue'
import Storage from './storage'

import { Navbar, Tabbar, TabItem, Cell} from 'mint-ui'
import { TabContainer, TabContainerItem } from 'mint-ui'

export default {
  name: 'app',
  data () {
    return {
      msg: 'Welcome to Your Vue.js App',
      content:"",
      isno:0,
      input:'',
      radio:'1',
      articles:[],
      ischange:'head-title',
      isClicked:false,
      selected:'1',
      lin:'/detail/0'
    }
  },
  // 
  computed:{
    nowdate:function() {
      var datee = new Date();
      console.log('Time:'+datee.toLocaleDateString());

      return datee.toLocaleDateString();
    }
  },
  methods: {
    readyInput:function (event) {
      // msg = content
      // data.msg = event.target.value // eventtarget
    },
    clickBtn:function (event) {
      if (this.Clicked) {
        this.ischange = 'head-title'
      }
      else {
        this.ischange = 'head-title1'
      }
      this.Clicked = !this.Clicked
      alert(this.a)
    },
    // 
    listenToRouterTemp: function (msg) {
      if (this.content === '') {
        this.content = msg;
      }
      else {
        this.content = '';
      }

    }
  },
  mounted:function(){
    this.$http.jsonp('https://api.douban.com/v2/movie/top250?count=10', {}, {
      header: {

      },
      emulateJSON: true
    }).then(function(response) {
      // 
      // console.log(this.articles);
      this.articles = response.data.subjects
      this.$nextTick(function(){
        console.log('DOM'+this);
      })

    }, function(response) {
      // 
      console.log(response)
    });

  },
  // 
  watch : {
    articles: {
      handler:function (oldVal, newVal) {
        console.log('');
        // 
        Storage.save(this.articles)
      },
      deep:true
    }
  },
  components:{footerTemp,routerTemp, Tabbar, Navbar, TabItem, TabContainer, TabContainerItem, Cell,drcell},
  // 
  // directives:{
  //   'focus':{
  //     inserted:function(el) {
  //       el.focus();
  //       console.log('focus');
  //     }
  //   }
  // },
}
</script>

<style>
  #app {
    font-family: 'Avenir', Helvetica, Arial, sans-serif;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
    /*text-align: center;*/
    color: #2c3e50;
    margin-top: 40px;
  }

  .mint-cell {
    height: 100px;
  }

  a {
    color: #42b983;
  }

  .head-title {

  }

  .head-title1 {
    animation: myfirst 5s;
    animation-direction: alternate;
    animation-iteration-count: infinite;
    -moz-animation: myfirst 5s;	/* Firefox */
    -moz-animation-direction: alternate;
    -moz-animation-iteration-count: infinite;
    -webkit-animation: myfirst 5s;	/* Safari  Chrome */
    -webkit-animation-direction: alternate;
    -webkit-animation-iteration-count: infinite;
    -o-animation: myfirst 5s;
    -o-animation-direction: alternate;
    -o-animation-iteration-count: infinite;
  }

  @keyframes myfirst
  {
  from {color:#20A0FF;}
  to {color:cyan;}
  }

  @-moz-keyframes myfirst /* Firefox */
  {
  from {color:#20A0FF;}
  to {color:cyan;}
  }

  @-webkit-keyframes myfirst /* Safari and Chrome */
  {
  from {color:#20A0FF;}
  to {color:cyan;}
  }

  @-o-keyframes myfirst /* Opera */
  {
  from {color:#20A0FF;}
  to {color:cyan;}
  }

</style>



